
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  position: relative;
  width: 100%;
  height: 300px;
}
body {
  margin: 5px;
  font-family: Helvetica, sans-serif;
}
.item {
  position: absolute;
  top: 5px;
  left: 5px;
  right: 5px;
  bottom: 5px;
  font-size: 1.2em;
  color: rgb(0, 158, 107);
}
.item:after {
  content: attr(index);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
}
.wf-transition {
  transition: opacity 0.3s ease;
  -webkit-transition: opacity 0.3s ease;
}
.wf-enter {
  opacity: 0;
}
.vue-waterfall-slot {
  padding: 2px;
}
.loading {
  width: 100px;
  height: 100px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -50px;
  margin-top: -50px;
  /* transition: all .2s ease; */
}
.loading > img {
  width: 100%;
  height: 100%;
  display: block;
}

.vue-waterfall {
  position: relative;
  /*overflow: hidden; cause clientWidth = 0 in IE if height not bigger than 0 */
}

.vue-waterfall-slot {
  position: absolute;
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

h1[data-v-d8ec41bc], h2[data-v-d8ec41bc] {
  font-weight: normal;
}
ul[data-v-d8ec41bc] {
  list-style-type: none;
  padding: 0;
}
li[data-v-d8ec41bc] {
  display: inline-block;
  margin: 0 10px;
}
a[data-v-d8ec41bc] {
  color: #42b983;
}
